@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
div#teamupdate .spinner {
  position: relative;
}
div#teamupdate .spinner .btn {
  display: none;
}
div#teamupdate .spinner:before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  width: 15px;
  height: 15px;
  margin-left: 7px;
  border-radius: 50%;
  border: 2px solid #ccc;
  border-top-color: #333;
  animation: spinner 0.6s linear infinite;
}
div#teamupdate .table th.number,
div#teamupdate .table td.number {
  text-align: center;
}
div#teamupdate .updateStatus .icon-checkmark {
  color: #398137;
}
div#teamupdate .updateStatus .icon-warning {
  color: #bd362f;
}
div#teamupdate .updateStatus .indicator {
  display: none;
  margin-left: 10px;
}
div#teamupdate .updateStatus .details {
  display: none;
}
div#teamupdate .updateStatus .details ul {
  list-style-type: none;
  margin: 0 0 0 10px;
  padding: 0;
}
div#teamupdate .updateStatus .show {
  display: block;
}

